import React, { Component } from 'react';
import { toast } from "react-toastify";
import { Modal } from 'react-bootstrap';
import Select from 'react-select';
import { renameCollection } from '../../../../Services/Actions/homeActions'
import { connect } from "react-redux";
class RenameCollectionModal extends Component {
    constructor(props) {
        super(props);
        this.state = {
            collectioName: props.collectionRename.name
        };
    }
    onClickCreate = event => {
        const { collectionRename, collectionData } = this.props
        var self = this
        const { collectioName } = this.state;
        if (collectioName && String(collectioName).trim().length > 0) {
            var toastID = toast.loading("Loading...")
            if (collectionData && Object.keys(collectionData).length > 0 && collectionData.collectionList && collectionData.collectionList.length > 0){
                const existsCollection = collectionData.collectionList.filter(collection => String(collection.name).trim().toLowerCase() == String(collectioName).trim().toLowerCase());
                if (existsCollection && existsCollection.length > 0) {
                    toast.update(toastID, { render: "Collection already exists.", type: "error", isLoading: false, autoClose: 3000 })
                    self.setState({ isRequesting: false });
                }
                else{
                    renameCollection({ name: collectioName, id: collectionRename.id }).then((response) => {
                        toast.update(toastID, { render: "Collection name has been updated.", type: "success", isLoading: false, autoClose: 3000 })
                        self.props.addCollectionCallbackWithAPI(false)
                    }).catch(ex=>{
                        const responseData = ex.response.data
                        toast.update(toastID, { render: responseData.message, type: "error", isLoading: false, autoClose: 3000 })
                    })
                }
            }
        }
        else{
            toast.error("Please enter valid collection name.")
        }
    }
    onChangeCollectionName = event => {
        this.setState({ collectioName: event.target.value })
    }

    handleClose = event => this.props.addCollectionCallback(false)

    render() {
        const { addCollectionModal, collectionRename } = this.props
        const { collectioName } = this.state
        return <Modal show={addCollectionModal} onHide={this.handleClose}
            aria-labelledby="contained-modal-title-vcenter"
            centered
        >
            <Modal.Header closeButton>
                <Modal.Title>Rename Collection</Modal.Title>
            </Modal.Header>
            <Modal.Body>
                <div className="mx-xl-4 px-2 px-lg-4 mx-0 pt-4">
                    <div className="row mb-4 mb-xl-5">
                        <label className="col-sm-3 text-muted mb-2">Collection Name</label>
                        <div className="col-sm-9">
                            <input type="text" name="company" className="form-control" placeholder="Enter collection name..." onChange={this.onChangeCollectionName} value={collectioName} />
                        </div>
                    </div>
                </div>
            </Modal.Body>
            <Modal.Footer className="p-3">
                <div className="mx-xl-4 px-2 px-lg-4 mx-0">
                    <button type="button" className="btn btn-secondary btn-sm btn-min-w me-3" onClick={this.handleClose}>Cancel</button>
                    <button type="button" className="btn btn-primary btn-sm btn-min-w" onClick={this.onClickCreate}>Rename</button>
                </div>
            </Modal.Footer>
        </Modal>
    }
}

const mapStateToProps = (state) => {
    return {
        collectionData: state.collection
    }
}

export default connect(mapStateToProps)(RenameCollectionModal);
